import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const { navBar, appid, appsecret } = app;
export default {
	data() {
		return {
			navBar: navBar,
			logo:'',
			mobile: '',
			password: ''
		};
	},
	methods: {
		clickLeft() {
			uni.switchTab({
				url: '/pages/tabar/index'
			});
		},
		pageUrl(data) {
			uni.navigateTo({
				url: data
			});
		},
		// 手机登录
		mobileLogin() {
			let { mobile, password } = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			if (!mobile) {
				uni.showToast({
					title: '手机号码不能为空，请输入手机号',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (!password) {
				uni.showToast({
					title: '密码不能为空，请输入密码',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (password.length < 6) {
				uni.showToast({
					title: '密码不能少于六位',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			let obj = {
				mobile: mobile,
				password: password,
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign(
				{
					sign: sign
				},
				obj
			);
			rs.postRequests('login', params, res => {
				let data = res.data;
				if (data.code == 200) {
					uni.showToast({
						title: '登录成功，将跳转到首页',
						duration: 2000,
						icon: 'none'
					});
					uni.setStorageSync('cdj_token', data.data.token);
					uni.setStorageSync('is_child', data.data.is_child);
					uni.setStorageSync('is_miniBind', data.data.is_appBind);
					uni.reLaunch({
						url: '/pages/tabar/index'
					});
				} else {
					uni.showToast({
						title: data.msg,
						duration: 2000,
						icon: 'none'
					});
				}
			});
		}
	},
	onShow() {
		let timeStamp = Math.round(new Date().getTime() / 1000);
		let obj = {
			appid: appid,
			timeStamp: timeStamp
		};
		let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		let params = Object.assign(
			{
				sign: sign
			},
			obj
		);
		rs.getRequests('indexAd', params, res => {
			let data = res.data;
			if (data.code == 200) {
				this.logo = data.data.logo;
			}
		});
	}
};
